/* # Global */
:root {
  --bg-main1-color: #FFFFFF;
  --bg-main2-color: #F7FAFC;
  --red-200-color: #FED7D7;
  --red-600-color: #E53E3E;
  --grey-100-color: #F7FAFC;
}
*, *:before, *:after {
  box-sizing: border-box;
}
html {
  font-size: 16px;
}
body {
  background-color: #FFFFFF;
  background-color: var(--bg-main1-color);
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  color: #333333;
}
h1 {
  margin: 0 0 1.5rem 0;
  font-size: 2rem;
  font-weight: 900;
}
.heading-1 > p {
  margin-bottom: 0;
}
h2, .heading-2 {
  margin: 0 0 1.5rem 0;
  font-size: 1.5rem;
  font-weight: 700;
}
h3, .heading-3 {
  margin: 0 0 1.25rem 0;
  font-size: 1.125rem;
  font-weight: 700;
}
h4, .heading-4 {
  margin: 0 0 1rem 0;
  font-size: 1rem;
  font-weight: 700;
}
p {
  margin: 0 0 1.5rem 0;
  line-height: 1.25rem;
}
a {
  text-decoration: none;
  color: #333;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.row {
  margin-left: -12px;
  margin-right: -12px;
  margin-bottom: 3rem;
}
.row:last-child {
  margin-bottom: 0;
}
.whiteBlock {
  padding: 1.5rem;
  background-color: #FFFFFF;
  background-color: var(--bg-main1-color);
  border-radius: 20px;
  box-shadow: 0 4px 10px rgba(160, 174, 192, 0.15);
}
button {
  padding: .25rem .75rem;
  font-weight: 400;
  font-size: .75rem;
  border-radius: 5px;
}
.heading-1 {
  padding: 2.25rem;
}

/* # Header */
.mainHeader {
  display: flex;
  width: 100%;
  height: 70px;
  padding-left: 2rem;
  padding-right: 3rem;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  background-color: #FFFFFF;
  background-color: var(--bg-main1-color);
  box-shadow: 400px 0 10px rgba(160, 174, 192, 0.1);
}
.sidebarHeader {
  display: flex;
  width: 21.75rem;
  padding: .375rem 0;
  align-items: center;
}
.sidebarHeader img {
  height: 58px;
  margin-right: 1rem;
}
.sidebarHeader .appType {
  padding: .175rem .375rem;
  font-size: .75rem;
  color: #fff;
  background-color: #D5D5D5;
  border-radius: 5px;
}
/* ## Nav */
.menuNav {
  display: flex;
  align-items: center;
}
.menuNav li a {
  display: flex;
  margin-right: 1rem;
  padding: .5rem;
  font-size: .875rem;
  align-items: center;
}
.menuNav li a:hover {
  color: #E53E3E;
  color: var(--red-600-color);
}
.menuNav li:nth-last-child {
  margin-bottom: 0;
}
.menuNav li a:before {
  margin-right: 1rem;
  font-family: 'documentation-ico';
  font-size: 1rem;
  font-weight: 400;
}
.menuNav li.menuHome a:before {
  content: '\e901';
}
.menuNav li.menuModeles a:before {
  content: '\e900';
}
.menuNav li.menuMaj a:before {
  content: '\e903';
}
.menuNav li.menuFav a:before {
  content: '\e902';
}
.menuNav li a.active {
  font-weight: 600;
  color: #E53E3E;
  color: var(--red-600-color);
}

/* ## Searchbox */
.searchBox {
  width: 25%;
  margin-left: auto;
  padding-right: 0;
}
.searchBox form {
  display: flex;
  border-radius: 20px;
  background-color: #F7FAFC;
  background-color: var(--bg-main2-color);
}
.searchBox form input {
  width: 100%;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  border: none;
  background-color: transparent;
}
.searchBox form input:focus {
  outline: none;
}
.searchBox form button {
  display: flex;
  width: 2.875rem;
  height: 2.875rem;
  align-items: center;
  justify-content: center;
  border: none;
  background: none;
}
.searchBox form button:before {
  content: '\e904';
  font-family: 'documentation-ico';
  font-size: 1.25rem;
  color: #E53E3E;
  color: var(--red-600-color);
}

/* # Sidebar */
.sidebar {
  display: flex;
  width: 23.75rem;
  height: calc(100vh - 70px);
  padding: 2rem 2rem 0;
  flex-direction: column;
  position: fixed;
  top: 70px;
  left: 0;
  z-index: 9999;
}
.sidebarMenu {
  margin: auto 0;
  padding: 0 1rem;
}
.sidebar nav ul li a {
  display: flex;
  padding: .75rem 0;
  align-items: center;
}
.sidebarMenu nav a:hover {
  color: #E53E3E;
  color: var(--red-600-color);
}
.sidebar .logoThem {
  width: 1.5rem;
  margin-right: 1rem;
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
}
.sidebar a:hover .logoThem {
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
}

/* ## Infos Account */
.sidebarAccount {
  display: flex;
  margin: 0 0 1.5rem 0;
  padding: 2rem;
  align-items: center;
}
.sidebarAccount:after {
  content: '\e905';
  margin-left: auto;
  font-family: 'documentation-ico';
}
.accountIcon {
  display: inline-block;
  width: 2rem;
  height: 2rem;
  margin-right: 1rem;
  background-color: #E53E3E;
  background-color: var(--red-600-color);
  color: #fff;
  border-radius: 50%;
  font-weight: 700;
  text-align: center;
  line-height: 2rem;
}

/* # App Main */
.appMain {
  margin-left: 23.75rem;
}
.mainInner.row {
  min-height: calc(100vh - 70px - 2rem);
  margin: 70px 0 2rem;
  padding: 2rem 2.25rem;
  background-color: #F7FAFC;
  background-color: var(--bg-main2-color);
  border-radius: 2rem 0 0 2rem;
  box-shadow: 0 0 10px rgba(160, 174, 192, 0.15) inset;
}

/* ## MainContent */
.themesList h2, .lastMaj h2, .indexMod h2, .theme .row h2 {
  margin-bottom: 1rem;
  padding-left: 1rem;
}

/* ## ThÃ¨mes */
.themesList .whiteBlock {
  min-height: 11.875rem;
  margin-bottom: 1.5rem;
}
.themInfo {
  display: flex;
  flex-direction: column;
}
.themInfo .logoThem {
  width: 2.5rem;
  margin-bottom: 1rem;
}
.nbUC {
  margin-top: auto;
}

/* ## Maj list */
.majTitle, .modTitle {
  display: flex;
  align-items: center;
}
.majTitle button, .modTitle button {
  margin: 0 0 1rem auto;
  border: 1px solid #E53E3E;
  border: 1px solid var(--red-600-color);
  background-color: #FFFFFF;
  background-color: var(--bg-main1-color);
  cursor: pointer;
}
.majTitle button a , .modTitle button a {
  color: #E53E3E;
  color: var(--red-600-color);
}
.majTitle button:hover, .modTitle button:hover {
  background-color: #E53E3E;
  background-color: var(--red-600-color);
}
.majTitle button:hover a , .modTitle button:hover a {
  color: #fff;
}

/* List UC */
li.itemUC {
  padding-bottom: 1rem;
}
li.itemUC:last-child {
  padding-bottom: 0;
}
.itemUCOuter {
  display: flex;
  align-items: center;
}
.itemUCOuter:before {
  content: "\e900";
  font-family: "documentation-ico";
  font-size: 1.25rem;
  padding-right: 1rem;
}
.titleUC {
  padding-bottom: .25rem;
  font-size: .875rem
}
.refInternet {
  font-size: .75rem;
  color: #E53E3E;
  color: var(--red-600-color);
}

/* # Documentation */
.documentation.theme .row .whiteBlock {
  margin-bottom: 0;
}
.themButton {
  margin-bottom: 2rem;
  padding: 1rem;
}
.themButton > div {
  display: flex;
  align-items: center;
}
.themButton .heading-4 {
  margin: 1rem 0 0 0;
}
.themButton .logoThem {
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
}
.themAll {
  font-size: .825rem;
}
.documentation .sidebar .heading-2, .documentation .sidebar nav ul {
  padding-left: 1rem;
}
.documentation .sidebar nav {
  padding-left: 1.75rem;
  overflow-y: auto;
}
.documentation .sidebar nav ul {
  border-left: 1px solid #A0AEC0;
}
.documentation .sidebarAccount {
  margin-top: auto;
}

/* # Responsive */
@media only screen and (max-width: 992px) {
  .sidebar {
    width: 23.75rem;
    padding: 0 1.5rem;
    overflow: hidden;
  }
  .appMain {
    margin-left: 7.125rem;
  }
}
@media only screen and (max-width: 1199px) {
  .searchBox {
    width: 2.875rem;
  }
  .searchBox form {
    border-radius: 50%;
  }
  .searchBox form input {
    display: none;
  }
}

/* Animation / Transitions */
.sidebar {
  transition: width ease .3s;
}
.appMain {
  transition: margin-left ease .3s;
}
